nav {
    background-color: purple;
}

.navbar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

a {
    text-decoration: none;
    color: white
}
 a:hover {
    text-decoration: underline;
    color: white
}

.navbar-brand {
    font-size: 35px;
}

.nav-item {
    font-size: 25px;
}
.jumbotron {
    background-image: url(/static/media/books.e2646e4c.jpg);
    background-color: white;
    height: 200px;
    background-size: cover;
}

h1 {
    color: white;
    font-size: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.submit-button {
    background-color: purple;
}

.form-group {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-control {
    max-width: 400px;
}


.form-title {
    font-size: 50px;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.body {
  font-family: 'Barlow', sans-serif;
}
